/*
*   Name: BizAgi Workportal Desktop New Case Widget Controller
*   Author: Juan Pablo Crossley
*   Comments:
*   -   This script will provide desktop overrides to implement the new case widget
*/

// Auto extend
bizagi.workportal.widgets.queries.extend("bizagi.workportal.widgets.queries.definition", {}, {

  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load templates
    self.loadTemplates({
      "queries": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries"),
      "queries-elements": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-elements"),
      "queries-empty-elements": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-empty-elements"),
      "queries-query-tree": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-query-tree"),
      "queries-query-confirm": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-confirm"),

      "queries-definition-elements": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-definition-elements"),
      "queries-definition-manage-elements": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-definition-manage-menu"),
      "queries-liveprocesses-query-element": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-definition-liveprocesses-menu"),
      "queries-flat-list": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-flat-list"),
      "queries-query-message-error": bizagi.getTemplate("bizagi.workportal.desktop.widget.queries").concat("#ui-bizagi-workportal-widget-queries-message-error"),

      useNewEngine: false });

  },

  /**
     *   To be overriden in each device to apply layouts
     */
  loadtemplates: function loadtemplates() {
    var self = this;
    self.mainTemplate = self.getTemplate("queries-definition-elements");
    self.mainTemplateManage = self.getTemplate("queries-definition-manage-elements");
    self.mainTemplateLiveProcessesQuery = self.getTemplate("queries-liveprocesses-query-element");
    self.emptyTemplate = self.getTemplate("queries-empty-elements");
    self.flatListTemplate = self.getTemplate("queries-flat-list");
    self.confirmTemplate = self.getTemplate("queries-query-confirm");
    self.messageErrorTemplate = self.getTemplate("queries-query-message-error");
  },

  postRender: function postRender() {
    var self = this;
    // Set bind event for last element
    self.configureTreeNavigation("#queries");
    // Render base categories
    self.renderQueries();
    // set vertical scroll
    self.scrollVertical({
      "autohide": false });

  },

  /**
      * return the new button for manageQueries if the user has permission
      */
  showManageQueries: function showManageQueries() {
    var self = this;
    var templateManage = self.mainTemplateManage;
    var content;
    var isManageQueries = bizagi.menuSecurity.QueriesPermissionManager === true && bizagi.util.isEnableFeature("enableTransferQueryOwnership");
    if (isManageQueries) {
      content = $.tmpl(templateManage, {});
    }
    return {
      manageContent: content,
      isManageQueries: isManageQueries };

  },

  /**
      * return the new button for liveProcessesQuery if the user has permission
      */
  getLiveProcessesQuery: function getLiveProcessesQuery() {
    var self = this;
    return self.hasLiveProcessesQuery() ? $.tmpl(self.mainTemplateLiveProcessesQuery, {}) : undefined;
  },

  /**
      * validate if the user has access to LP query
      */
  hasLiveProcessesQuery: function hasLiveProcessesQuery() {
    return bizagi.menuSecurity.AdhocUserGroupReport === true;
  },

  /**
      * merge templates and verify content undefined
      */
  mergeQueryWithLpContent: function mergeQueryWithLpContent(queryContent, lpContent) {
    var content = queryContent;
    if (queryContent !== undefined && lpContent !== undefined) {
      content = $.merge(queryContent, lpContent);
    } else if (lpContent !== undefined) {
      content = lpContent;
    }
    return content;
  },

  /**
      * return query menu options before saved query
      */
  beforeSavedQueriesContent: function beforeSavedQueriesContent(data, appendElement, idParentQuery) {
    var self = this;
    var manage = self.showManageQueries();
    var queryContent = manage.manageContent;
    var mergeData;
    if (data.length >= 1) {
      // Check if have appendElement
      mergeData = appendElement !== undefined ? $.merge(appendElement, data) : data;
      var tmplRendered = $.tmpl(self.mainTemplate, mergeData, {
        checkChildNode: self.checkChildNode });

      if (self.validateMenu(queryContent, idParentQuery)) {
        queryContent = tmplRendered;
      } else {
        queryContent.append(tmplRendered);
      }
    } else if (!manage.isManageQueries && !self.hasLiveProcessesQuery()) {
      queryContent = $.tmpl(self.emptyTemplate, mergeData);
    }
    return queryContent;
  },

  /**
      * return the new button for manageQueries if the user has permission
      */
  addEventToMangeQueries: function addEventToMangeQueries(self) {
    self.publish("showDialogWidget", {
      widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_QUERIES_MANAGEMENT,
      data: {},
      maximize: true,
      closeVisible: false,
      modalParameters: {
        title: bizagi.localization.getResource("workportal-widget-queries-management-title"),
        width: 790,
        height: 540,
        refreshInbox: false } });


  },

  /**
      * go to Reports (LP query) in quickprocess
      */
  addEventToLPQuery: function addEventToLPQuery(self) {
    if ($(document).find('iframe').length > 0 && $(document).find('iframe')[0].contentWindow.location.href.indexOf('quickprocess') !== -1) {
      bizagi.util.liveProcessesEvents.routeEvents.route = '/reports';
      document.dispatchEvent(bizagi.util.liveProcessesEvents.routeEvents);
      bizagi.workportal.desktop.popup.closePopupInstance();
    } else {
      self.publish("changeWidget", {
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_PROFLY,
        data: {
          route: "reports",
          params: { module: 'none' } } });


    }
  },

  validateMenu: function validateMenu(queryContent, idParentQuery) {
    var enterToFolder = (queryContent == undefined || idParentQuery !== undefined) && idParentQuery !== "-1";
    var leaveFolder = queryContent == undefined && idParentQuery === "-1";
    return enterToFolder || leaveFolder;
  },

  /**
      * Check if user has permission to BizagiQueries and do request to obtain QueriesDefinitions
      * If the user do not have permission return an empty array in a promise 
      * @param idParentQuery 
      * @param securityBizagiQueries flag permission to BizagiQueries
      */
  getQueriesDefinitions: function getQueriesDefinitions(idParentQuery, securityBizagiQueries) {
    var self = this,
    request = function request() {
      return self.dataService.getQueriesDefinitions({
        idNode: idParentQuery });

    },
    noRequest = function noRequest() {
      var defer = new $.Deferred();
      defer.resolve([]);
      return defer.promise();
    };
    var call = securityBizagiQueries ? request : noRequest;
    return call();
  },

  /**
      * Check if user has permission to BizagiQueries and do request to obtain StoredQueries
      * If the user do not have permission return an empty array in a promise 
      * @param securityBizagiQueries flag permission to BizagiQueries
      */
  getStoredQueryFormList: function getStoredQueryFormList(securityBizagiQueries) {
    var self = this,
    request = function request() {
      return self.dataService.getStoredQueryFormList();
    },
    noRequest = function noRequest() {
      var defer = new $.Deferred();
      defer.resolve({ storedQueryForms: [] });
      return defer.promise();
    };
    var call = securityBizagiQueries ? request : noRequest;
    return call();
  },

  /**
     * Render List categories for each idCategory
     */
  renderQueries: function renderQueries(idParentQuery, appendElement) {
    var self = this;
    var securityBizagiQueries = bizagi.menuSecurity.BizagiQueries;
    var content = self.getContent();
    var queriesContainer = $("#queries", content);
    var queryContent;

    $.when(self.getQueriesDefinitions(idParentQuery, securityBizagiQueries)).done(function (data) {
      queriesContainer.empty();
      queryContent = self.beforeSavedQueriesContent(data || [], appendElement, idParentQuery);

      //if ((typeof idParentQuery === "undefined" || idParentQuery === "-1") && bizagi.util.parseBoolean(bizagi.override.disableFrankensteinQueryForms)) {
      if (typeof idParentQuery === "undefined" || idParentQuery === "-1") {
        $.when(self.getStoredQueryFormList(securityBizagiQueries)).done(function (storedQueryForm) {
          if (storedQueryForm.storedQueryForms.length > 0) {
            var stored = $.tmpl(self.mainTemplate, {
              queryType: "-2",
              queryDisplayName: self.getResource("workportal-widget-queries-stored-query-forms"),
              nodes: [],
              guidForm: "stored" });

            queryContent = $.merge(queryContent, stored);
            self.storedQueryForm = storedQueryForm.storedQueryForms;
          }
          queriesContainer.append(self.mergeQueryWithLpContent(queryContent, self.getLiveProcessesQuery()));
          self.configureNavTree(queriesContainer);
        });
      } else {
        queryContent.appendTo(queriesContainer);
      }
      // set actions to rendered html 
      self.configureNavTree(queriesContainer);
    });
  },

  /**
     * Render List stored query Form
     */
  renderStoredQueries: function renderStoredQueries() {
    var self = this;
    var content = self.getContent();
    var flatListTemplate = self.flatListTemplate;
    var queriesContainer = $("#queries", content);
    var storedQueryFormContent;
    storedQueryFormContent = $.tmpl(flatListTemplate, { storedQueryForm: self.storedQueryForm, queryNameFormatter: self.queryNameFormatter });
    storedQueryFormContent.appendTo(queriesContainer);
  },

  /**
      * Format name of a stored query showed when is a shared query form anonymized user
      * @param {*} currentName 
      */
  queryNameFormatter: function queryNameFormatter(currentName) {
    if (currentName) {
      var _regex = /UserDeleted_[a-zA-z0-9-]*/gi,
      _resource = bizagi.localization.getResource("workportal-general-word-anonymized");
      currentName = currentName.replace(_regex, _resource.toLowerCase());
    }
    return currentName;
  },

  configureNavTree: function configureNavTree(queriesContainer) {
    var self = this;
    var confirm = self.getTemplate("queries-query-confirm");

    //Bind for manage queries
    $("li#manageQueries", queriesContainer).click(function () {
      self.addEventToMangeQueries(self);
    });

    //Bind for manage queries
    $("li#manageLiveProcessesQuery", queriesContainer).click(function () {
      self.addEventToLPQuery(self);
    });

    // Bind for list elements
    $("li:not(#manageQueries,#manageLiveProcessesQuery)", queriesContainer).click(function () {
      var idNode = $(this).children("#idNode").val();
      var guidQueryForm = $(this).children("#guidQueryForm").val();
      var guidForm = $(this).children("#guidForm").val();
      var guidContext = $(this).children("#guidContext").val();
      var queryType = $(this).children("#queryType").val();
      var nodesLength = $(this).children("#nodesLength").val();
      var queryDisplayName = $(this).children("#queryDisplayName").val();
      var headerTemplate = self.getTemplate("queries-query-tree");
      var categoryTree = $("#categoryTree");
      var notMigratedUrl = $(this).children("#notMigratedUrl").val();

      if (queryType == "-2") {
        self.renderStoredQueries();
        $.tmpl(headerTemplate, {
          idParentQuery: "-2",
          queryDisplayName: queryDisplayName }).
        appendTo(categoryTree);
        self.configureTreeNavigation("#queries");
      } else
      if (nodesLength > 0) {
        // Is category with link
        // Add element for tree navigation
        $.tmpl(headerTemplate, {
          idParentQuery: idNode,
          queryDisplayName: queryDisplayName }).
        appendTo(categoryTree);
        // Set bind event for last element
        self.configureTreeNavigation("#queries");
        self.renderQueries(idNode);
      } else {
        // Is Process
        bizagi.workportal.desktop.popup.closePopupInstance();
        var title = $("#queryDisplayName", this).val();
        self.showQueryFormPopup({
          "guidForm": guidForm,
          "guidEntity": guidContext,
          "title": title,
          "queryFormAction": "renderQueryForm",
          "queryType": queryType,
          "notMigratedUrl": notMigratedUrl });

      }
    });

    $(".deleteGAButton").click(function (e) {
      e.stopPropagation();
      var confirmContent = $.tmpl(self.confirmTemplate);
      var confirmContentParams = {
        resizable: true,
        modal: true,
        title: self.getResource("workportal-widget-queries-confirm-title"),
        buttons: [] };

      var notMigratedUrlDelete = $("#notMigratedUrlDelete", $(this).parent()).val();

      confirmContentParams.buttons = [
      {
        text: self.getResource("workportal-widget-queries-confirm-delete"),
        click: function click() {
          $(this).dialog("close");
          //checks if it should use the url based method to delete the query
          if (notMigratedUrlDelete) {
            $.destroy(notMigratedUrlDelete);
          }
          bizagi.workportal.desktop.popup.closePopupInstance();
        } },

      {
        text: self.getResource("workportal-widget-queries-confirm-cancel"),
        click: function click() {
          $(this).dialog("close");
        } }];


      $(confirmContent).dialog(confirmContentParams);

    });
  },

  /**
     * Bind tree navigation
     */
  configureTreeNavigation: function configureTreeNavigation(container) {
    var self = this;
    var content = self.getContent();
    var categoryTree = $("#categoryTree", content);
    var queriesContainer = $(container, content);
    // Bind header events
    $("li:last-child", categoryTree).click(function () {
      // Remove all elements
      $(this).nextAll().remove();
      var idParent = $(this).children("#idParent").val();
      // Render query tree again
      if (idParent == "-2") {
        self.renderStoredQueries();
      } else {
        self.renderQueries(idParent);
      }
    });

    $("li.storedQuery", queriesContainer).click(function () {
      var guid = $(this).data("guid");
      var id = $(this).data("id");
      var idNode = $(this).data("idnode");
      var paramsStoredQuery;
      var result = $.grep(self.storedQueryForm, function (e) {
        return e.idNode == idNode;
      });
      if (result.length == 1) {
        paramsStoredQuery = result[0];

        self.showQueryFormPopup({
          "idStoredQuery": guid,
          "idQuery": 1,
          "title": paramsStoredQuery.definition.name,
          "paramsStoredQuery": paramsStoredQuery,
          "queryFormAction": "execute",
          "notMigratedUrl": paramsStoredQuery.notMigratedUrl ? paramsStoredQuery.notMigratedUrl.execute : "" });

      }
    });

    // Bind for action elements (Edit and delete buttons)
    $(".editButton", container).click(function (e) {
      e.stopPropagation();
      var queryId = "-1";
      var guid = $(this).parent().data("guid");
      var idStoredQuery = $(this).parent().data("id");
      var idNode = $(this).parent().data("idnode");
      var paramsStoredQuery;
      var result = $.grep(self.storedQueryForm, function (e) {
        return e.idNode == idNode;
      });
      if (result.length == 1) {
        paramsStoredQuery = result[0];
        self.showQueryFormPopup({
          "queryFormAction": "edit",
          "idStoredQuery": idStoredQuery == "-1" ? "" : idStoredQuery,
          "paramsStoredQuery": paramsStoredQuery,
          "title": paramsStoredQuery.definition.name,
          "notMigratedUrl": paramsStoredQuery.notMigratedUrl ? paramsStoredQuery.notMigratedUrl.edit : "" });

        bizagi.workportal.desktop.popup.closePopupInstance();
      }
    });
    $(".deleteButton", container).click(function (e) {
      e.stopPropagation();
      var queryId = "-1";
      var guid = $(this).parent().data("guid");
      var idStoredQuery = $(this).parent().data("id");
      var idNode = $(this).parent().data("idnode");
      var confirmContent, paramsStoredQuery, confirmContentParams;

      var messageErrorForm = self.getResource("workportal-widget-admin-entities-message-migrate");
      messageErrorForm = messageErrorForm.replace("{build}", bizagi.loader.productBuildToAbout);

      var result = $.grep(self.storedQueryForm, function (e) {
        return e.idNode == idNode;
      });
      if (result.length == 1) {
        paramsStoredQuery = result[0];

        confirmContentParams = {
          resizable: true,
          modal: true,
          title: self.getResource("workportal-widget-queries-confirm-title"),
          buttons: [] };

        var p_sessionId = bizagi.cookie("JSESSIONID");
        if (p_sessionId !== null && paramsStoredQuery.notMigratedUrl) {
          confirmContent = $.tmpl(self.messageErrorTemplate, { messageError: messageErrorForm });
        } else
        {
          confirmContent = $.tmpl(self.confirmTemplate);

          confirmContentParams.buttons = [
          {
            text: self.getResource("workportal-widget-queries-confirm-delete"),
            click: function click() {
              $(this).dialog("close");
              //checks if it should use the url based method to delete the query
              if (paramsStoredQuery.notMigratedUrl && paramsStoredQuery.notMigratedUrl["delete"]) {
                $.get("App/" + paramsStoredQuery.notMigratedUrl["delete"]);
              } else {
                self.dataService.deleteStoredQueryForm({ idStoredQueryForm: idStoredQuery });
              }
              bizagi.workportal.desktop.popup.closePopupInstance();
              self.publish("refreshQueryFormShortCut");
            } },

          {
            text: self.getResource("workportal-widget-queries-confirm-cancel"),
            click: function click() {
              $(this).dialog("close");
            } }];


        }

        // Open dialog with confirm message
        $(confirmContent).dialog(confirmContentParams);
      }
    });
  },

  scrollVertical: function scrollVertical() {
    var self = this;
    var content = self.getContent();
    $("#queries", content).bizagiScrollbar({
      "autohide": false });

  },

  /*
     *   Show new case popup to create a case
     */
  showQueryFormPopup: function showQueryFormPopup(arg) {
    var self = this;
    var queryFormAction = arg.queryFormAction || "";
    var title = arg.title || "";
    var queryType = arg.queryType || "";
    var guidForm = arg.guidForm || "";
    var guidEntity = arg.guidEntity || "";
    var paramsStoredQuery = arg.paramsStoredQuery || {};
    // If the same popup is opened close it
    if (self.currentPopup == "queryform") {
      bizagi.workportal.desktop.popup.closePopupInstance();
      return;
    }
    // Shows a popup widget
    self.currentPopup = "queryform";
    self.publish("showDialogWidget", {
      widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_QUERYFORM,
      queryFormAction: queryFormAction,
      queryType: queryType,
      paramsStoredQuery: paramsStoredQuery,
      modalParameters: {
        title: title,
        id: "Queries" },

      closeVisible: false,
      guidForm: guidForm,
      guidEntity: guidEntity,
      notMigratedUrl: arg.notMigratedUrl || "",
      maximizeOnly: true,
      editQueryForm: function editQueryForm(params) {
        self.currentPopup = undefined;
        self.showPopUpEditQueryForm(params);

      },
      executeQueryForm: function executeQueryForm(params) {
        self.currentPopup = undefined;
        self.showPopUpResultsQueryForm(params);
      } });

  },

  showPopUpEditQueryForm: function showPopUpEditQueryForm(params) {
    var self = this;
    var content = self.getContent();

    $.when(self.dataService.getStoredQueryFormList()).done(function (storedQueryForm) {
      self.storedQueryForm = storedQueryForm.storedQueryForms;
      self.publish("refreshQueryFormShortCut");
      if (params && params.response) {
        var idQueryForm = params.response;
        self.editQueryForm(idQueryForm);
      }
    });
  },

  showPopUpResultsQueryForm: function showPopUpResultsQueryForm(params) {
    var self = this;
    var content = self.getContent();

    $.when(self.dataService.getStoredQueryFormList()).done(function (storedQueryForm) {
      self.storedQueryForm = storedQueryForm.storedQueryForms;
      self.publish("refreshQueryFormShortCut");
      if (params && params.response) {
        var idQueryForm = params.response;
        self.executeQueryForm(idQueryForm);
      }
    });
  },

  editQueryForm: function editQueryForm(idQueryForm) {
    var self = this;
    var paramsStoredQuery = self.getParamsStoredQuery(idQueryForm);
    if (paramsStoredQuery.length == 1) {
      paramsStoredQuery = paramsStoredQuery[0];
      self.showQueryFormPopup({
        "queryFormAction": "edit",
        "idStoredQuery": idQueryForm == "-1" ? "" : idQueryForm,
        "paramsStoredQuery": paramsStoredQuery,
        "title": paramsStoredQuery.definition.name,
        "notMigratedUrl": paramsStoredQuery.notMigratedUrl ? paramsStoredQuery.notMigratedUrl.edit : "" });

    }
  },
  executeQueryForm: function executeQueryForm(idQueryForm) {
    var self = this;
    var paramsStoredQuery = self.getParamsStoredQuery(idQueryForm);
    if (paramsStoredQuery.length == 1) {
      paramsStoredQuery = paramsStoredQuery[0];
      self.showQueryFormPopup({
        "idStoredQuery": paramsStoredQuery.guid,
        "idQuery": 1,
        "title": paramsStoredQuery.definition.name,
        "paramsStoredQuery": paramsStoredQuery,
        "queryFormAction": "execute",
        "notMigratedUrl": paramsStoredQuery.notMigratedUrl ? paramsStoredQuery.notMigratedUrl.execute : "" });

    }
  },

  getParamsStoredQuery: function getParamsStoredQuery(idQueryForm) {
    var self = this;
    return $.grep(self.storedQueryForm, function (e) {
      return e.id == idQueryForm;
    });
  } });