/**
 * Name: Bizagi Workportal Users Modal
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.queries.management", {}, {
  /**
                                                                                               * Constructor
                                                                                               */
  init: function init(workportalFacade, dataService, params) {
    var self = this;
    self.selectedUsers = ["", ""];

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load template
    self.loadTemplates({
      "queries-management": bizagi.getTemplate("bizagi.workportal.desktop.widgets.queries.management").concat("#queries-management"),
      "queries-management-loader": bizagi.getTemplate("bizagi.workportal.desktop.widgets.queries.management").concat("#ui-bizagi-workportal-widget-queries-management-loader"),
      "queries-management-table": bizagi.getTemplate("bizagi.workportal.desktop.widgets.queries.management").concat("#ui-bizagi-workportal-widget-queries-management-table") });


  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate('queries-management');
    self.content = template.render();
    return self.content;
  },

  postRender: function postRender() {
    var self = this;
    self.setupSearchusersButton();
    self.loaderTmpl = self.getTemplate("queries-management-loader");
    self.loader = self.loaderTmpl.render();
  },

  /**
      * Set the fixed head table of the reporting details
      */
  fixedHeadTable: function fixedHeadTable() {
    var self = this;
    var fixedHeadTableElement = $(".ui-bizagi-wp-queries-management-content-parent-tableHeadFixed > .ui-bizagi-wp-queries-management-content-tableHeadFixed", self.content);
    fixedHeadTableElement.find("table:first").floatThead({
      position: 'fixed',
      zIndex: 1,
      scrollContainer: function scrollContainer() {
        return fixedHeadTableElement;
      } });


    self.onResizeModalEvent();
  },

  clean: function clean() {
    var self = this;
  },

  /**
      * find the maximize button and when the user clicks it the body trigger resize, this is necesary for fixedHeadTable, and recall again
      */
  onResizeModalEvent: function onResizeModalEvent() {
    var self = this;
    self.getContent().parents(".ui-dialog").find(".ui-dialog-titlebar-maximize").click(function () {
      $("body").trigger("resize");
      self.onResizeModalEvent();
    });
  },

  /*
     * initialize the users search form widget
     */
  setupUsersSearchForm: function setupUsersSearchForm(input, user) {
    var self = this;
    var content = self.getContent();
    var def = $.Deferred();
    $(".queries-management-content", content).hide();


    self.usersTable = new bizagi.workportal.widgets.userstable(self.workportalFacade, self.dataService, $.extend(self.params, {
      canvas: $("#dinamicContent", content),
      userLinkLabel: [bizagi.localization.getResource("workportal-widget-admin-defaults-assignation-select-link")],
      parentDef: def,
      showBackButton: true }));

    self.usersTable.render();

    $.when(def).done(function (userId, userName, guid) {
      var params = {
        action: "Applications",
        userId: userId };

      if (user == 0) {
        self.dataService.getApplicationCategoriesList(params).
        done(function (applicationList) {
          if (applicationList.items.length > 0) {
            self.allProcess = true;
            self.haveProcess = true;
            if (guid !== undefined) {
              self.getQueries(guid);
            }
          }

        }).fail(function () {
        });
      }
      $("#dinamicContent", content).html("");
      $(".queries-management-content", content).show();
      if (userName) {
        $(input, content).val(userName);
        self.showHideClearIcon(input, 'block');
      }
      if (guid) {
        self.selectedUsers[user] = guid;
      }
    });
  },
  showNotifier: function showNotifier(message) {
    var self = this;
    var notifier = window.parent.bizagi.injector.get('notifier');
    notifier.showSucessMessage(message, '');
  },
  showLoading: function showLoading() {
    var self = this;
    $(".queries-management-container").append(self.loader);
  },
  endLoading: function endLoading() {
    var self = this;
    $(self.loader).remove();
  },
  clearInput: function clearInput(input, content) {
    var self = this;
    var position = input == "#txtTextUserSource" ? 0 : 1;
    self.showHideClearIcon(input, 'none');
    $(input, content).val("");
    self.selectedUsers[position] = "";
  },
  showHideClearIcon: function showHideClearIcon(id, display) {
    var self = this;
    var content = self.getContent();
    $(id + " ~ span", content).css("display", display);
  },
  getQueries: function getQueries(guid) {
    var self = this;
    var params = {};
    params.guidUser = guid;
    self.showLoading();
    self.dataService.getManagementQueries(params).
    done(function (response) {
      self.queries = response.storedQueryForms;
      self.endLoading();
      if (self.queries.length > 0) {
        var dataTable = $.map(response.storedQueryForms, function (value) {
          var name = value.definition.name;
          var description = value.definition.description;
          return {
            name: name,
            description: description };

        });
        $(".emptyQueries", self.content).hide();
        $(".queries-content", self.content).show();
        self.renderTable(dataTable);
      } else
      {
        $(".emptyQueriesText").text(bizagi.localization.getResource("workportal-widget-queries-management-list-empty-no-queries"));
      }
    }).
    fail(function (error) {
      self.endLoading();
      bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-assign-fail"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
      bizagi.log(error);
    });
  },
  renderTable: function renderTable(dataTable) {

    var self = this;
    var template = self.getTemplate('queries-management-table');
    var data = {
      data: dataTable };

    var contentTable = template.render(data);
    $(".queries-content.ui-bizagi-wp-queries-management-content-tableHeadFixed").html(contentTable);
    self.fixedHeadTable();
  },
  validateUsers: function validateUsers() {
    var self = this;
    var resp = true;
    if (self.selectedUsers[0] == "" || self.selectedUsers[1] == "") {
      bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-fail-you-must-select-both-users-to-continue"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
      resp = false;
    } else if (self.selectedUsers[0] == self.selectedUsers[1]) {
      bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-fail-users-must-be-different"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
      resp = false;
    } else
    if (self.queries.length == 0) {
      bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-queries-management-list-empty-no-queries"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
      resp = false;
    }
    return resp;
  },

  transferQueries: function transferQueries() {
    var self = this;
    if (self.validateUsers()) {
      var buttons = [
      {
        label: bizagi.localization.getResource("workportal-widget-queries-management-transfer-confirmation-modal-no-button"),
        action: "reject" },

      {
        label: bizagi.localization.getResource("workportal-widget-queries-management-transfer-confirmation-modal-button"),
        action: "resolve" }];


      var customOptions = {
        dialogClass: "widget-queries-management-transfer-confirm",
        width: 400,
        minWidth: 400 };


      $.when(bizagi.showConfirmationBox(bizagi.localization.getResource("workportal-widget-queries-management-transfer-confirmation-modal-description"), bizagi.localization.getResource("workportal-general-word-information"), false, buttons, customOptions)).
      done(function () {
        self.showLoading();
        var params = {
          guidUserSource: self.selectedUsers[0],
          guidUserTarget: self.selectedUsers[1] };

        self.dataService.transferQueries(params).
        done(function () {
          self.endLoading();
          $(".emptyQueriesText").text(bizagi.localization.getResource("workportal-widget-queries-management-list-empty-no-queries"));
          $(".queries-content", self.content).hide();
          $(".emptyQueries", self.content).show();
          self.queries = [];
          self.showNotifier(bizagi.localization.getResource("workportal-widget-queries-management-queries-transferred-successfully"));
        }).
        fail(function (error) {
          self.endLoading();
          bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-queries-management-queries-transferred-fail"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
          bizagi.log(error);
        });

      });
    }
  },

  /****************** EVENTS ***********************/


  setupSearchusersButton: function setupSearchusersButton() {
    var self = this,
    content = self.getContent();

    $("#btn-searchUserSource", content).click(function () {
      self.setupUsersSearchForm("#txtTextUserSource", 0);
    });
    $("#btn-searchUserTarget", content).click(function () {
      self.setupUsersSearchForm("#txtTextUserTarget", 1);
    });

    $("#btn-clearUserSource", content).click(function () {
      self.clearInput("#txtTextUserSource", content);
      $(".emptyQueriesText").text(bizagi.localization.getResource("workportal-widget-queries-management-list-empty-no-selected-user"));
      $(".queries-content", self.content).hide();
      $(".emptyQueries", self.content).show();
    });
    $("#btn-clearUserTarget", content).click(function () {
      self.clearInput("#txtTextUserTarget", content);
    });

    $("#btn-transferQueries", content).click(function () {
      self.transferQueries();
    });
  } });



bizagi.injector.register('bizagi.workportal.widgets.queries.management', ['workportalFacade', 'dataService', bizagi.workportal.widgets.queries.management]);